"use client"

import { useState } from 'react';

const EditorPage = () => {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    content: ''
  });
  const [error, setError] = useState<string | null>(null);

  // 表单字段变化时更新状态
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  // 提交表单时的处理
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // 这里暂时没有与后端交互的逻辑，之后可以加入
    console.log('提交的数据：', formData);
    alert('文章已提交');
    // 重置表单
    setFormData({ title: '', description: '', content: '' });
  };

  return (
    <div className="max-w-3xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6">编辑文章</h1>
      {error && <p className="text-red-500">{error}</p>}
      
      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label htmlFor="title" className="block text-sm font-medium text-gray-700">标题</label>
          <input
            type="text"
            id="title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            className="mt-2 p-2 border border-gray-300 rounded w-full"
            placeholder="请输入文章标题"
            required
          />
        </div>

        <div className="mb-4">
          <label htmlFor="description" className="block text-sm font-medium text-gray-700">简短描述</label>
          <input
            type="text"
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            className="mt-2 p-2 border border-gray-300 rounded w-full"
            placeholder="请输入简短描述"
            required
          />
        </div>

        <div className="mb-4">
          <label htmlFor="content" className="block text-sm font-medium text-gray-700">内容</label>
          <textarea
            id="content"
            name="content"
            value={formData.content}
            onChange={handleChange}
            className="mt-2 p-2 border border-gray-300 rounded w-full"
            rows={6}
            placeholder="请输入文章内容"
            required
          />
        </div>

        <div className="flex justify-end mt-6">
          <button
            type="submit"
            className="btn btn-primary"
          >
            保存文章
          </button>
        </div>
      </form>
    </div>
  );
};

export default EditorPage;
